}

code {
  font-family: 'Open Sans', sans-serif;
}

.navbar-bg {
  background-color: rgb(118, 219, 219);
  font-size: 1em;
  z-index: 99;
  position: relative;

}
.navbar li {
  padding: 0 2rem;
}

.navbar li a:hover{
  text-shadow: 0 0.3rem 0.4rem grey;

