.home-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -450px;
}

.home-container  p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  
}

.slider{
  position: relative;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slide-image{
  width: 1000px;
  height: 80vh! important;
  border-radius: 6px;
}
.right-arrow{
  position: absolute;
  top: 50%;
  right: 32px;
  
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.left-arrow{
  position: absolute;
  top: 50%;
  left: 32px;

  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active{
  opacity: 1;
  transition-duration: 1s;
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}


