:root{
    --body-font: 'Open Sans', sans-serif;
    --font-medium: 400;
    --font-semi-bold: 700;
    --primary-color: rgb(8, 153, 179);
    --white-color: #fff;
    --black-color: black;
    --body-color: rgb(82, 136, 114);
    --border-color: #e4e8ee;

}

*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
    box-sizing: border-box;
}


 body{
    position: relative;
    
    align-items: center;
    justify-content: center;
    min-height: 100vh;
} 

.wrapper{
    padding: 50px 25px 0;
    max-width: 668px;
    width: 100%;
    margin: auto; 
} 

 .wrapper::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-position: center center;
    background-size: cover;
    min-height: 100vh;
    z-index: -1;

} 

.wrapper .heading{
    text-align: center;
    padding-bottom: 48px;

} 
.wrapper .heading h1{
    font-size: 42px;
    font-weight: 700;
    font-weight: var(--font-semi-bold);
    line-height: 52px;

} 

.wrapper .form{
    max-width: 350px;
    width: 100%;
    margin: auto;
} 
.wrapper .form-group{
    margin-bottom: 15px;
} 

.wrapper .form-group input{
    display: block;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.1px;
    padding: 11px 15px;
    height: 48px;
    border-radius: 5px;
    color: black;
    color: var(--black-color);
    border: 1px solid #e4e8ee;
    border: 1px solid var(--border-color);
    box-shadow: none;
    width: 100%;
} 

.wrapper .form-group input:focus{
    outline: none;

} 
.wrapper .form-group input::-webkit-input-placeholder{
    color: black;
    color: var(--black-color);
    font-weight: 400;
    font-weight: var(--font-medium);
    font-size: 14px;

} 
.wrapper .form-group input:-ms-input-placeholder{
    color: black;
    color: var(--black-color);
    font-weight: 400;
    font-weight: var(--font-medium);
    font-size: 14px;

} 
.wrapper .form-group input::placeholder{
    color: black;
    color: var(--black-color);
    font-weight: 400;
    font-weight: var(--font-medium);
    font-size: 14px;

} 

.wrapper .btn{
    width: 100%;
    margin: 15px 0 30px;
    font-size: 14px;
    line-height: 22px;
    font-weight: 700;
    font-weight: var(--font-semi-bold);
    padding: 12px 29px;
    height: 48px;
    text-transform: uppercase;
    color: #fff;
    color: var(--white-color);
    background-color: rgb(8, 153, 179);
    background-color: var(--primary-color);
    border: none;
    cursor: pointer;
    text-align: center;

} 

.places{
    padding: 6 4rem;
    margin: 2rem;


}

.places h1{
    padding-top: 2.5rem;
    padding-bottom: 2rem;
    text-transform: uppercase;
    text-align: center;
    font-weight: 700;
}
.card .card-body .card-img-top{
    border-radius: 6%;

}
.card:hover{
    box-shadow: 0 0.2rem 0.4rem 0 rgb(20, 48, 34);

}
.card-price{
    font-weight: 600;
}
.card-img-top{
    width: 100%! important;
    height: 200px! important;
    object-fit: cover;
}

.card-title{
    font-family: 'Josefin Sans', sans-serif;
    justify-content: center;
    text-align: center;
    font-weight: Semi-bold 600npm strat;
}
.card-body{
    background-color: rgb(161, 236, 165);
}
.footer_container{
    background-color: #120f25;
    padding: 2rem 0 1rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.btn{
    background-color: aqua;
    padding: 4px;
}

.btn:hover{
    transition: 0.3s ease-out;
    text-shadow: 0 0.3rem 0.4rem rgb(58, 56, 56);
    
}

.subscription{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: rgb(230, 210, 33);
}

.subcription-heading{
    margin-bottom: 24px;
    font-size: 24px;
}

.unsubscribe-text{
    margin-bottom: 24px;
    font-size: 15px;
}

.footer-input{
    padding: 0px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;

}

.footer-links{
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;

}

.footer-link-wrapper{
    display: flex;

}

.footer-link-items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
}

.footer-link-items h2{
    margin-bottom: 16px;
}

.footer-link-items >h2{
    color: #e2d13a;
}

.footer-link-items a{
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.footer-link-items a:hover{
    color: #f4f5cc;
    transition: 0.3s ease-out;
}


